import {profileAPI} from '../API/api'

const ADD_POST = 'my-app/profile/ADD-POST';
const UPDATE_NEW_POST_TEXT = 'my-app/profile/UPDATE-NEW-POST-TEXT';
const SET_USER_PROFILE = 'my-app/profile/SET_USER_PROFILE';
const GET_STATUS = 'my-app/profile/GET_STATUS';

let initialState = {
  postsData: [
    { id: 1, message: "My first post", likesCount: 1},
    { id: 2, message: "My second post", likesCount: 20},
    { id: 3, message: "My third post", likesCount: 30},
  ],
  profile: null,
  status: '',
}

const profileReducer = (state = initialState, action) => {
  switch(action.type) {
    case ADD_POST:
      let newPost = {
        id: 5,
        message: action.add_post,
        likesCount: 0,
      }

      return {
        ...state,
        postsData: [...state.postsData, newPost],
      }

    case UPDATE_NEW_POST_TEXT:
      return {
        ...state,
        newPostText: action.newText,
      }

    case SET_USER_PROFILE:
      return {
        ...state,
        profile: action.profile,
      }
    
    case GET_STATUS:
      return {
        ...state,
        status: action.status,
      }
    
    default:
      return state;
  }
}

export const addPostActionCreator = (add_post) => {
  return {
    type: ADD_POST,
    add_post
  }
}

export const updateNewPostTextActionCreator = (text) => {
  return {
    type: UPDATE_NEW_POST_TEXT, 
    newText: text
  }

}
export const setUserProfile = (profile) => {
  return {
    type: SET_USER_PROFILE, 
    profile: profile
  }
}

export const setStatus = (status) => {
  return {
    type: GET_STATUS, 
    status: status
  }
}

export const getUserProfile = (userId) => 
  async (dispatch) => {
    let profile = await profileAPI.getProfile(userId);
    dispatch(setUserProfile(profile));
  }


export const getStatus = (userId) => 
  async (dispatch) => {
    let status = await profileAPI.getStatus(userId);
    dispatch(setStatus(status));
  }

export const updateStatus = (status) => 
  async (dispatch) => {
    let response = await profileAPI.updateStatus(status);

    if(response.data.resultCode === 0) {
      dispatch(setStatus(status));
    }
  }


export default profileReducer;