import {userAPI} from '../API/api'

const FOLLOW = 'my-app/users/FOLLOW';
const UNFOLLOW = 'my-app/users/UNFOLLOW';
const SET_USERS = 'my-app/users/SET_USERS';
const SET_CURRENT_PAGE = 'my-app/users/SET_CURRENT_PAGE';
const SET_TOTAL_USERS_COUNT = 'my-app/users/SET_TOTAL_USERS_COUNT';
const TOGGLE_IS_FETCHING = 'my-app/users/TOGGLE_IS_FETCHING';
const TOGGLE_IS_FOLLOWING = 'my-app/users/TOGGLE_IS_FOLLOWING';

let initialState = {
  usersData: [
//     { id: 1, fullName: "User1", status: 'First status', location: {city: 'City1', country: 'Country1'}, followed: true, photoUser: 'https://i.yapx.ru/Ra8I0.jpg'},
//     { id: 2, fullName: "User2", status: 'Second status', location: {city: 'City2', country: 'Country2'}, followed: false, photoUser: 'https://i.yapx.ru/Ra8I0.jpg'},
//     { id: 3, fullName: "User3", status: 'Third status', location: {city: 'City3', country: 'Country3'}, followed: true, photoUser: 'https://i.yapx.ru/Ra8I0.jpg'},
  ],
  pageSize: 10,
  currentPage: 1,
  totalUsersCount: 0,
  isFetching: false,
  isFollowingInProgress: [],
}

const usersReducer = (state = initialState, action) => {
  switch(action.type) {
    case FOLLOW:
      return {
        ...state,
        usersData: state.usersData.map(user => {
          if(user.id === action.userId) {
            return {...user, followed: true}
          }
          return user;
        })
      }
    case UNFOLLOW:
      return {
        ...state,
        usersData: state.usersData.map(user => {
          if(user.id === action.userId) {
            return {...user, followed: false}
          }
          return user;
        })
      }
    case SET_USERS: {
      return {...state, usersData: action.users}
    }
    case SET_CURRENT_PAGE: {
      return {...state, currentPage: action.currentPage}
    }
    case SET_TOTAL_USERS_COUNT: {
      return {...state, totalUsersCount: action.totalUsersCount}
    }
    case TOGGLE_IS_FETCHING: {
      return {...state, isFetching: action.isFetching}
    }
    case TOGGLE_IS_FOLLOWING: {
      return {...state, 
        isFollowingInProgress: action.isFollowingInProgress 
        ? [...state.isFollowingInProgress, action.userId]
        : state.isFollowingInProgress.filter(id => id !== action.userId)
      }
    }
    default:
      return state;
  }
}

export const followSuccess = (userId) => {
  return {
    type: FOLLOW,
    userId
  }
}

export const unFollowSuccess = (userId) => {
  return {
    type: UNFOLLOW,
    userId
  }
}

export const setUsers = (users) => {
  return {
    type: SET_USERS,
    users
  }
}

export const setCurrentPage = (currentPage) => {
  return {
    type: SET_CURRENT_PAGE,
    currentPage: currentPage,
  }
}

export const setTotalUsersCount = (UsersCount) => {
  return {
    type: SET_TOTAL_USERS_COUNT,
    totalUsersCount: UsersCount,
  }
}

export const toggleIsFetching = (isFetching) => {
  return {
    type: TOGGLE_IS_FETCHING,
    isFetching: isFetching,
  }

}
export const toggleIsFollowingProgress = (isFollowingInProgress, userId) => {
  return {
    type: TOGGLE_IS_FOLLOWING,
    isFollowingInProgress: isFollowingInProgress,
    userId
  }
}

export const requestUsers = (currentPage, pageSize) => 
  async (dispatch) => {
    dispatch(toggleIsFetching(true));
    let users = await userAPI.getUsers(currentPage, pageSize);

    dispatch(setUsers(users.items));
    dispatch(setTotalUsersCount(users.totalCount));
    dispatch(toggleIsFetching(false));
  }


export const changePageNumber = (pageNumber, pageSize) => 
  async (dispatch) => {
    dispatch(setCurrentPage(pageNumber));
    dispatch(toggleIsFetching(true));

    let users = await userAPI.getUsers(pageNumber, pageSize);

    dispatch(setUsers(users.items));
    dispatch(toggleIsFetching(false));
  }

const followUnfollowFlow = async (dispatch, userId, apiMethod, actionCreator) => {
  dispatch(toggleIsFollowingProgress(true, userId));
  let response = await apiMethod(userId);

  if(response.data.resultCode === 0) {
    dispatch(actionCreator(userId))
  }
  dispatch(toggleIsFollowingProgress(false, userId));
}

export const follow = (userId) => 
  async (dispatch) => {
    followUnfollowFlow(dispatch, userId, userAPI.follow.bind(userAPI), followSuccess);
  }


export const unFollow = (userId) => 
  async (dispatch) => {
    followUnfollowFlow(dispatch, userId, userAPI.unFollow.bind(userAPI), unFollowSuccess);
  }

export default usersReducer;