import style from './Header.module.css'
import { NavLink } from 'react-router-dom';

const Header = (props) => {
  return (
    <header className={style.header}>
      <div className={style.logo}>
        <img src="https://static.vecteezy.com/system/resources/previews/001/191/987/non_2x/circle-logo-png.png" alt="Logo" className="logo__img" />
      </div>
      <div className={style.login}>
        {props.isAuth
          ? 
          <div>
            <NavLink to={'/profile'} className={style.login__link}>{props.login}</NavLink>
            <button onClick={props.logout}>Log out</button>
          </div>
          : <NavLink to={'/login'} className={style.login__link}>Login</NavLink>
        }
        
        
      </div>
  </header>
  );
}

export default Header;