import React from "react";
import { Formik, Form, Field, ErrorMessage } from 'formik';
import { connect } from 'react-redux';
import {login, } from '../../redux/auth-reducer'
import {Navigate } from "react-router-dom";
import style from './Login.module.css'

const LoginForm = (props) => {
  return (
    <Formik
      initialValues={{ email: '', password: '' , }}
      validate={values => {
        const errors = {};
        if (!values.email) {
          errors.email = 'E-mail required';
        } else if (
          !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
        ) {
          errors.email = 'Invalid email address';
        }
        if (!values.password) {
          errors.password = 'Password required';
        }
        return errors;
      }}
      onSubmit={(values, { setSubmitting, setStatus }) => {
          props.login(values.email, values.password, values.rememberMe, setStatus)
      }}
    >
      {({ isSubmitting, status }) => (
        <Form>
          <div  className={style.inputGroup}>
            <label htmlFor="email" className={style.fieldLabel}>E-mail</label>
            <Field type="email" name="email"  className={style.fieldInput} />
            <ErrorMessage name="email" component="div" />
          </div>
          <div  className={style.inputGroup}>
            <label htmlFor="password" className={style.fieldLabel}>Password</label>
            <Field type="password" name="password"  className={style.fieldInput} />
            <ErrorMessage name="password" component="div" />
          </div>
          <div>
            <div  htmlFor="rememberMe" className={style.inputGroup}>
              <label className={style.fieldLabel}>
                <Field type="checkbox" name="rememberMe"  className={style.fieldCheckbox}/>
                Remember me
              </label>
            </div>
            <div>{status}</div>
          <button type="submit" disabled={isSubmitting}>
            Login
          </button>
          </div>
        </Form>
      )}
    </Formik>

  );
}
const Login = (props) => {
  if (props.isAuth) {
    return <Navigate to='/profile'></Navigate>
  }
  return (
    <>
      <h1>Login</h1>
      <LoginForm 
      login={props.login}
      />
    </>
  );
}

const mapStateToProps = (state) => {
  return {
    isAuth: state.auth.isAuth,
  }
}

export default connect(mapStateToProps, {login})(Login);