const ADD_MESSAGE = 'ADD-MESSAGE';
const UPDATE_NEW_MESSAGE_TEXT = 'my-app/dialogs/UPDATE-NEW-MESSAGE-TEXT';

let initialState = {
  dialogsData: [
    { id: 1, name: "Name11", avatar:'https://dthezntil550i.cloudfront.net/04/latest/041806182101290800005662547/1280_960/285a3b7a-6f32-4d15-a158-6fc89ca20129.png', active: true},
    { id: 2, name: "Name2", avatar:'https://vsekidki.ru/uploads/posts/2016-08/1470735121_lecdaa3axdc.jpg'},
    { id: 3, name: "Name3", avatar:'https://shapka-youtube.ru/wp-content/uploads/2021/02/avatarka-dlya-skaypa-dlya-parney.jpg'},
  ],
  messagesData: [
    { id: 1, message: "Hi!"},
    { id: 2, message: "How are you?"},
    { id: 3, message: "How you doing?"},
  ],
  // newMessageText: '',
}

const dialogsReducer = (state = initialState, action) => {
  switch(action.type) {
    case ADD_MESSAGE:
      let newMessage = {
        id: 5,
        message: action.add_message,
      }
  
      // stateCopy.messagesData = state.messagesData;
      // stateCopy.messagesData.push(newMessage);
      // stateCopy.newMessageText = '';
      return {
        ...state,
        // newMessageText: '',
        messagesData: [...state.messagesData, newMessage] //       
      };

    case UPDATE_NEW_MESSAGE_TEXT:
      // stateCopy = {...state}
      // stateCopy.newMessageText = action.newText;
      return {
        ...state,
        newMessageText: action.newText
      };
    
    default:
      return state;
  }
}

export const addMessageActionCreator = (add_message) => {
  return {
    type: ADD_MESSAGE,
    add_message
  }
}

export const updateNewMessageTextActionCreator = (text) => {
  return {
    type: UPDATE_NEW_MESSAGE_TEXT, 
    newText: text
  }
}

export default dialogsReducer;